.newbb {
}

.newbb-header {
}

.anchor:before {
  content: ' ';
  height: 60px;
  margin-top: -60px;
  display: block;
}

.newbb-menu {
}

.newbb-header-icons {
}

.newbb-header-icons div {
  margin-bottom: 10px;
}

.newbb-category-list {
}

.newbb-category-list a.pull-right {
  margin-left: 5px;
}

.newbb-list-foruns {
  border-bottom: 1px solid #EAEAEA;
  padding-bottom: 5px;
}

.xoops-newbb-list-foruns:last-child {
  border-bottom: none;
}

.newbb-viewtopic {
}

.newbb-tagbar {
}

.newbb-online-users {
}

.newbb-quick-reply textarea {
  max-height: 150px;
  width: 100%;
}

.newbb-forum-title {
}

.newbb-forum-title strong {
  font-size: 1.5em;
  color: #BBB;
}

.newbb-forum-title a.btn {
  color: #FFF;
}

.newbb-subforum {
}

.newbb-subforum ul.subforum-loop {
  background: #EEE;
  margin: 0;
  padding: 10px 0 10px 0;
  border-bottom: 1px solid #E1E1E1;
  border-top: 1px solid #FFF;
}

.newbb-topicslist {
}

.newbb-topiclist-loop {
}

.newbb-topiclist-header {
  background: #F6F6F6;
  padding: 10px 0 10px 0;
  border-bottom: 1px solid #FFF;
}

.newbb-topiclist-itens {
  border-bottom: 1px solid #FFF;
  border-top: 1px solid #DDD;
  padding: 10px 0 10px 0;
}

.newbb-topiclist-itens.even {
  background: #FAFAFA;
}

.newbb-topiclist-itens.odd {
  background: #F6F6F6;
}

.newbb-thread {
  background: #EEE;
  border: 1px solid #DADADA;
}

.newbb-post-anchor {
  background: #09C;
  color: #FFF;
  position: absolute;
  right: 0;
  top: 0;
  padding: 3px;
  font-size: 0.8em;
}

.newbb-user-signature {
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px dotted #DADADA;
  width: 100%;
  color: #BBB;
  font-style: italic;
  padding: 5px;
}

.newbb-thread-attachment {
  margin-top: 20px;
}

.newbb-thread-attachment strong,
.newbb-thread-attachment small {
  display: block;
}

.newbb-user-data {
}

.newbb-message-area {
  position: relative;
  padding-bottom: 40px;
  padding-left: 0;
}

.newbb-topic-id {
  position: absolute;
  bottom: 0;
  right: 0;
}

.newbb-links {
  margin-top: 5px;
  border-bottom: 1px dotted #E6E6E6;
  padding-bottom: 2px;
}

.read-topic {
  color: #999;
}

.read-topic:hover {
  color: #333;
}

.new-topic {
  font-weight: bold;
}

.forum-search {
  margin-top: 10px;
}
